body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 60px;
}

.App-header {
  background-color: #222;
  height: 70px;
  padding: 15px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.autoComplete {
  -ms-flex-positive: 1;
      flex-grow: 1;
  height: 250px;
}

.autoComplete-container {
  -ms-flex-positive: 1;
      flex-grow: 1;
  position: relative;
  width: 80%;
  margin-left: 10%;
}

.autoComplete-paper {
  position: fixed;
  z-index: 1;
  margin-top: 2px;
}
.autoComplete-chip {
  margin: 2px 4px;
}

.autoComplete-inputRoot {
  padding-top: 10px;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}


@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.chart-container {
  position: relative;
  margin: auto;
  height: 80vh;
  width: 80vw;
}

.domainCard {
  width: 24% !important;
  margin: 6px !important;
  color: #222 !important;
  border: 3px solid #00C853;
}

.domainCard h4 {
  font-size: 1.1em !important;
}

.nav-drawer {
  width: 250px;
}

.nav-drawer > *[class^='MuiPaper-']{
  min-width: 240px;
}

.drawerPaper {
  width: 250px;
}

.drawerHeader {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: end;
      justify-content: flex-end;
  padding: 0 8px;

}

.fof {
  margin: 1em;
  overflow: auto;
}

.fofTable {
  min-width: 700px;
}

h1 a {
  color: #FFFFFF !important;
}


@media only screen and (max-device-width: 667px) {
  .domainCard {
    width: 80vw !important;
    margin: 2px !important;
    border: 2px solid #00C853;
  }
}


